//
import VueRouter from 'vue-router'
//Vue
import footer from '../../components/footer.vue' //
import index from '../../components/index.vue'    //
import ctm_service from '../../components/ctm_service.vue'  //
import classify from '../../components/classify.vue'    //
import suit from '../../components/suit.vue'           //
import activity from '../../components/activity.vue'    //
import shoppingcar from '../../components/shoppingcar.vue'  //

import left_nav from '../../components/left_nav.vue'  //
import my from '../../components/my.vue'  //
import search from '../../components/search.vue'  //

import quality_ass from '../../components/quality_ass.vue'  //
import l_delivery from '../../components/l_delivery.vue'  //
import s_day_return from '../../components/s_day_return.vue'  //
import aftersale from '../../components/aftersale.vue'  //

import purchase_qlt from '../../components/purchase_qlt.vue' //

import bottom from '../../components/bottom.vue'  //
import heater from '../../components/heater.vue'  //
import shoping from '../../components/shoping.vue' //
import goods from '../../components/goods.vue' //
import moreact from '../../components/moreact.vue' //
import classification from '../../components/classification.vue'//
import details from '../../components/details.vue'//

import ypApp from '../../components/ypApp.vue'//app
import login from '../../components/login.vue'//


//
export default new VueRouter({
    routes: [
        {
            path: '/',
            component: footer,
            children: [
                {
                    // 
                    path: '/index',
                    component: index
                },
                {
                    //
                    path: '/classify',
                    component: classify
                },
                {
                    //
                    path: '/activity',
                    component: activity
                },
                {
                    //
                    path: '/suit',
                    component: suit
                }
            ],
            redirect: '/index'
        },
        {
            //
            path: '/ctm_service',
            component: ctm_service
        },
        {
            //
            path: '/shoppingcar',
            component: shoppingcar
        },
        {
            //
            path: '/quality_ass',
            component: quality_ass
        },
        {
            //
            path: '/l_delivery',
            component: l_delivery
        },
        {
            //
            path: '/s_day_return',
            component: s_day_return
        },
        {
            //
            path: '/aftersale',
            component: aftersale
        },
        {
            //
            path: '/search',
            component: search
        },
        {
            //
            path: '/left_nav',
            component: left_nav
        },
        {
            //
            path:'/my',
            component: my
        },
        {
            //
            path: '/purchase_qlt',
            component: purchase_qlt
        },
        {
            path:'/shoping/:sh',
            component:shoping
        },
        {
            path:'/goods',
            component:goods,
        },
        {
            path:'/moreact',
            component:moreact,
        },
        {
            path:'/classification',
            component:classification
        },
        {
            path:'/details',
            component:details
        },
        {
            path:'/ypApp',
            component:ypApp
        },
        {
            path:'/login',
            component:login
        },
        //
        {
            path: '/*',
            redirect: '/index'
        }
    ]
})


